import Vue from 'vue'
import VueRouter from 'vue-router'
import Dad from '@/views/MyDad.vue'
import Letters from '@/views/AllLetters.vue'
import WriteLetter from '@/views/WriteLetter.vue'
import Me from '@/views/IntroduceMyself.vue'

Vue.use(VueRouter)

const routes = [
  {
    path: '/',
    name: 'DefaultPage',
    component: Letters
  },
  {
    path: '/seungsoo',
    name: 'Dad',
    component: Dad
  },
  {
    path: '/writes',
    name: 'WriteLetter',
    component: WriteLetter
  },
  {
    path: '/hyesun',
    name: 'Developer',
    component: Me
  }
]

const router = new VueRouter({
  mode: 'history',
  base: process.env.BASE_URL,
  routes
})

export default router
