<template>
  <div class="main">
    <v-container>
      <div class="messages">
        <v-row justify="center">
          <v-col v-if="flowers.length != 0">
            <div class="text-truncate">{{ flowers[0].w }}</div>
            <img class="flower" :src="flowers[0].f">
          </v-col>
        </v-row>
        <v-row justify="center" v-if="flowers.length >= 2">
          <v-col cols=2>
            <div class="text-truncate">{{ flowers[1].w }}</div>
            <img class="flower" :src="flowers[1].f">
          </v-col>
          <v-col v-if="flowers.length >= 3" cols=2>
            <div class="text-truncate">{{ flowers[2].w }}</div>
            <img class="flower" :src="flowers[2].f">
          </v-col>
          <v-col v-else cols=2></v-col>
        </v-row>
        <v-row justify="center" v-if="flowers.length >= 4">
          <v-col cols=2>
            <div class="text-truncate">{{ flowers[3].w }}</div>
            <img class="flower" :src="flowers[3].f">
          </v-col>
          <v-col v-if="flowers.length >= 5" cols=2>
            <div class="text-truncate">{{ flowers[4].w }}</div>
            <img class="flower" :src="flowers[4].f">
          </v-col>
          <v-col v-else cols=2></v-col>
          <v-col v-if="flowers.length >= 6" cols=2>
            <div class="text-truncate">{{ flowers[5].w }}</div>
            <img class="flower" :src="flowers[5].f">
          </v-col>
          <v-col v-else cols=2></v-col>
        </v-row>
        <v-row justify="center" v-if="flowers.length >=7">
          <v-col cols=2>
            <div class="text-truncate">{{ flowers[6].w }}</div>
            <img class="flower" :src="flowers[6].f">
          </v-col>
          <v-col v-if="flowers.length >= 8" cols=2>
            <div class="text-truncate">{{ flowers[7].w }}</div>
            <img class="flower" :src="flowers[7].f">
          </v-col>
          <v-col v-else cols=2></v-col>
          <v-col v-if="flowers.length >= 9" cols=2>
            <div class="text-truncate">{{ flowers[8].w }}</div>
            <img class="flower" :src="flowers[8].f">
          </v-col>
          <v-col v-else cols=2></v-col>
        </v-row>
      </div>
    </v-container>
  </div>
</template>

<script>
import firestore from '../main'
import { collection, query, orderBy, limit, getDocs } from 'firebase/firestore'

export default {
  name: 'AllLetters',
  components: {
  },
  data() {
    return {
      flowers: []
    }
  },
  methods: {
    fetchFlowers() {
      const q = query(collection(firestore, "letters"), orderBy("t", "desc"), limit(10))
      getDocs(q)
        .then((docs) => { docs.forEach((doc) => { 
          const m = {
            f: 0,
            w: doc.data().w,
            m: doc.data().m,
          }
          if (doc.data().f == 1) {
            m.f = "@/assets/red.png"
          } else if (doc.data().f == 2) {
            m.f = "@/assets/orange.png"
          } else {
            m.f = "@/assets/pink.png"
          }
          this.flowers.push(m) })})
    }
  },
  created() {
    setTimeout(() => {
      this.fetchFlowers()
    }, 500)
  }
}
</script>

<style>
  .main {
    min-width: 100vw;
    min-height: 100vh;
    background-position: center;
    background-image: url('@/assets/flowers.jpg');
  }
  .messages {
    font-family: 'GangwonEdu_OTFBoldA';
    color: white;
    padding-top: 10rem;
  }
  .flower {
    width: 50px;
    height: 50px;
  }
</style>